import { Flex, WhiteSpace } from 'antd-mobile';
import React from 'react'
import { withRouter } from 'react-router-dom'
import { antionCreators } from './actionCreators'
import { connect } from 'react-redux'

// const PlaceHolder = ({ className = '', ...restProps }) => (
//   <div className={`${className} placeholder`} {...restProps}>创意陶瓷果篮</div>
// );

class FlexExample extends React.Component {
  constructor(props) {
    super();
  }
  componentDidMount = () => {
    fetch('https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist')
      .then((res) => res.json())
      .then((myJson) => {
        console.log(myJson);
        let value1 = myJson.data;
        this.props.dispatch({
          type: 'add',
          value: value1
        })
        this.setState({
          imgObj: value1
        })
        this.props.dispatch({
          type: 'ad',
          value: value1
        })
      });
  }

  getnumber(x) {
    let imx = this.state.imgObj;
    for (var i = x; i < imx.length; i++) {
      return (
        <Flex>
          <Flex.Item><img style={{ width: '200px', height: '200px'}}
            onClick={() => {
              this.props.dispatch({
                type: 'ad',
                index: i
              })
              this.props.history.push("/S2")
            }}
            src={imx[i].img} ></img>
            <p>{imx[i].title}</p>
            <p>{imx[i].price}</p>
          </Flex.Item>
          <Flex.Item><img style={{ width: '200px', height: '200px' }}
            onClick={() => {
              this.props.dispatch({
                type: 'ad',
                index: i + 1
              })
              this.props.history.push("/S2")
            }}
            src={imx[i + 1].img}></img>
            {/* <p>{arr[i + 1].title}</p> */}
            <p>{imx[i + 1].price}</p>
          </Flex.Item>
        </Flex>
      )
    }
  }
  render() {
    console.log("liujiaxi:", this.props.userinfo);
    return (
      <div>
        <div className="container0">
          <div className="title0">
            {this.getnumber(0)}
            {this.getnumber(2)}
            {this.getnumber(4)}
            {this.getnumber(6)}
            {this.getnumber(8)}
          </div>
        </div>
      </div>
    )
  }
}


