let a={
    datalist:[],
    shoucang:[],
    userinfo:''
}
const homereducer = (state = a,action)=>{
    switch(action.type){
        case 'ActionCreators':
            // Object.assign({},state,{uname:'123'})
            return Object.assign({},state,{userinfo:action.uname});
        default:
            return state;
    }
}
export default homereducer;